
.navbar-dark {
  background-color: #1D4771;
}

.row-header{
  margin:0px auto;
  padding:0px auto;
}

.jumbotron{
  padding: 70px;
  background-color: #3f81c4;
  color: floralwhite;
  border-radius: 0px;
  margin-bottom: 0px;
}
.footer{
  color: floralwhite;
  padding: 50px 5px 5px 5px;
  margin-top: 70px;
  background-color: #3f81c4;
}
.footer a{
  color:floralwhite;
}
.list-unstyled{
  
}
.list-unstyled a{
  color:floralwhite;
}
.row-copyright{
  margin-top: 20px;
}
.social-custom{
  margin-top: 20px;
  text-align: center;
}
.nav-custom a{
  color: floralwhite;
}

.btn-login{
  border: 0px ;
  text-transform: capitalize;
  color: floralwhite;
}
.btn-login:hover {
  color: rgba(255, 250, 240, 0.774);
  text-decoration: none;
}
.btn-login:enabled {
  color: floralwhite;
  border: 0px ;
  text-decoration: none;
}
.btn {
  text-transform: capitalize;
}
.loading-spinner{
  text-align: center;
  color: #3f81c4;
}

.table-body-stripe tr:nth-child(even){
  background-color: #9ac4ee62;
}

.bread-crumb{
  width: 100%;
  margin-bottom: 5%;
}

.product-comment-card{
  padding: 20px;
  margin: 15px;
}

.jumptron-logo-img{
  text-align: center;
}
.products-menu{
  justify-content: center;
}
@media (max-width: 576px){
  .products-menu{
    padding-left: 20px;
    padding-right: 20px;
  }
}
.align-text-center{
  text-align: center;
}
.img-leader-aboutus{
  border-radius: 90px;
}
.product-price-menu-item{
  text-align: center;
  color:rgb(101, 114, 114);
}


.page-enter {
  opacity: 0.01;
  transform: translateX(-100%);
}

.page-enter-active {
  opacity: 1;
  transform: translateX(0%);
  transition: all 300ms ease-in;
}

.page-exit {
  opacity: 1;
  transform: translateX(0%);
}

.page-exit-active {
  opacity: 0.01;
  transform: translateX(100%);
  transition: all 300ms ease-out;
}

.product-details-img{
  height: 250px;
}

@media (max-width:768px){
  .product-details-img{
    height: 170px;
  }
  
}

@media (max-width:576px){
  .product-details-img{
    height: 250px;
  }
  .header-title{
    text-align: center;
  }
  .header-subtitle{
    text-align: center;
  }
}

.aboutus-leader-description{
  text-align: justify;
  text-justify: inter-word;
}